.slider{
    height:100vh !important ;
}

.slider-frame {
    height:100% !important ;
    position: relative;
}

.slider .slider-container{
    height: 100vh;
    margin:0 auto;
    width: 100%;
    position: relative;
    display: block;
}

.black-wrapper{
    width:100%;
    height: 100%;
    z-index: 2000;
    position: absolute;
    background-color: rgba(0, 0, 0, 0.76);

    /* background: -moz-linear-gradient(left, rgba(0,0,0,1) 0%, rgba(0,0,0,0.2) 49%, rgba(0,0,0,1)  100%);
    background: -webkit-linear-gradient(left, rgba(0,0,0,1) 0%, rgba(0,0,0,0.2) 49%, rgba(0,0,0,1)  100%);
    background: -o-linear-gradient(left, rgba(0,0,0,1) 0%, rgba(0,0,0,0.2) 49%, rgba(0,0,0,1)  100%);
    background: -ms-linear-gradient(left, rgba(0,0,0,1) 0%, rgba(0,0,0,0.2) 49%, rgba(0,0,0,1)  100%);
    background: linear-gradient(to right, rgba(0,0,0,1) 0%, rgba(0,0,0,0.2) 49%, rgba(0,0,0,1)  100%); */
}
.image-desc-container{
    z-index: 1000;
    position: absolute;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    color: aliceblue;
    margin: 0 auto;
    width: 100%;
    text-align: right;
    font-size: 60px;
    padding:30px 120px;
   
}

.image-desc-container p{
    font-family: Lobster;
}

.image-container{
    height: 100%;
    margin:0 auto;
    width: 100%;
    position: absolute;
    display: block;
}
.image-container img {
    cursor: default;
    width: 100%;
    display: block;
    /* align-content: center; */
    text-align: center;
}

.slider .fa {
    cursor: pointer;
    color:white;
}

.slider .fa-chevron-left{
    margin-left: 30px;
}

.slider .fa-chevron-right{
    margin-right: 30px;
}






