.fest{
    border-radius: 15px; 
    width: 100%;
    height: 500px;
    background: burlywood;
    position: relative;
    /* display: inline-flex; */
    overflow: hidden;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.fest > video{
    width: 100%;
    display: block;
    position: absolute;
}
.fest-overlay{
    color: aliceblue;
    position: absolute;
    z-index: 10;
    display: -ms-flexbox;
    display: flex;
    width:100%;
    height: 100%;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    background: -webkit-radial-gradient(rgba(0,0,0,0.5),rgba(0,0,0,1));
    background: -o-radial-gradient(rgba(0,0,0,0.5),rgba(0,0,0,1));
    background: radial-gradient(rgba(0,0,0,0.5),rgba(0,0,0,1));
}
