
.event-slider{
	width: 100%; 
	position: relative;
	display: -ms-flexbox;
	display: flex;
	
}

.slider-image{
	width: 70%;
	max-height: auto;
	margin: 2%;
	opacity: 1;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: end;
	    justify-content: end;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
}

.slider-image-inactive{
	display: none;
}


.slider-image > img{
	border-radius: 10px;
	width: 100%;
	-webkit-box-shadow: 0 3px 7px -1px rgba(0, 0, 0, 0.9);
	        box-shadow: 0 3px 7px -1px rgba(0, 0, 0, 0.9);
}

.slider-toggler{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
}

.image-button {
	margin: 18px 0 0 18px;
	border: 3px solid #999;
	display: -ms-flexbox;
	display: flex;
	cursor: pointer;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
	opacity: 0.6;
}

.active{
	border-color: #666;
	opacity: 1;
	-webkit-transform: scale(1.1);
	    -ms-transform: scale(1.1);
	        transform: scale(1.1);
}




